}
.tab-menu {

   
}

ul.tabs {
text-align: center;
    display: grid;  /* グリッドレイアウトを適用 */
    grid-template-columns: repeat(2fr, 1fr);  /* 3列のグリッドを作成（1frは1つの分けられた単位） */
    gap: rem(32);  /* アイテム間の間隔を10pxに設定 */
    font-size: rem(14);
 
  @include mq("md") {
    grid-template-columns: repeat(4, 1fr);  /* 3列のグリッドを作成（1frは1つの分けられた単位） */
    gap: rem(32);  /* アイテム間の間隔を10pxに設定 */
    font-size: rem(14);
    max-width: rem(874);
    }
  
}



.tabs__all {
    font-size: rem(14);
    border: rem(1) solid $green;
    padding: rem(6) rem(69);
    margin-right: rem(32);
    max-width: 162px;
    @include mq("md") {
        font-size: rem(14);
        border: rem(1) solid $green;
        padding: rem(6) rem(69);
        margin-right: rem(32);
    }
}

.tabs__text {
    font-size: rem(14);
    border: rem(1) solid $green;
    color: $green;
    padding: rem(6) rem(25);
margin-right: rem(32);
max-width: rem(176);
@include mq("md") {
    font-size: rem(14);
    border: rem(1) solid $green;
    color: $green;
    padding: rem(6) rem(25);
margin-right: rem(32);
}
}

.tabs__text:hover {

    color: $white;

}
.tabs .active .button2__item {
    background-color: $green; /* ボタン背景色 */
    color: $white;           /* ボタン文字色 */
    border: rem(1) solid $green; /* ボーダーも変える場合 */
}

.tabs .active .button2__item:hover {
    background-color: darken($green, 10%); /* ホバー時の色を少し濃く */
}
