@use "global" as *;

.archive {
    padding: 0px;
  }
  .archive__title {
    font-size: 24px;
    font-weight: bold;
  }
  .archive__post {
    margin-bottom: 20px;
    border-bottom: 1px solid #ddd;
    padding-bottom: 10px;
  }
  .archive__post-title {
    font-size: 20px;
    color: #333;
  }
  .archive__pagination {
    text-align: center;
    margin-top: 20px;
  }
  

  